___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Open Sans;
  background-color: #082032;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  gap: 24px;
}

.card {
  width: 220px;
  height: 220px;
  border-radius: 8px;
  color: white;
  background-color: #334756;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 5px;
  padding: 15px;
  line-height: 20px;
}

.card svg {
  width: 80px;
  height: 80px;
  margin: 0;
}

#cards {
  display: grid;
  justify-content: center;
  gap: 10px;
  grid-template-columns: repeat(auto-fit, 250px);
  width: 100vw;
  max-width: 770px;
  margin-top: 12px;
}

h1 {
  margin: 18px 0 0 0;
  color: lightgray;
}

#weather-info {
  display: flex;
  flex-direction: column;
  gap: 3px;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
  justify-content: center;
}

#message {
  color: orangered;
  font-size: 1.4rem;
}

#units {
  font-size: 1.4rem;
  display: flex;
  gap: 18px;
}

#search-box {
  border: none;
  border-radius: 5px 0 0 5px;
  background-color: #bdd3e6;
  height: 28px;
  width: 240px;
  padding: 1px 6px;
  font-size: 1.1rem;
  transition: background-color 0.3s ease-in;
}

#search-box:hover {
  background-color: #86b3da;
}

#search-box:focus {
  background-color: #86b3da;
}

button[type="submit"] {
  border: none;
  height: 30px;
  width: 48px;
  border-radius: 0 5px 5px 0;
  font-size: 1.2rem;
  font-weight: bold;
  color: #082032;
  background-color: #bdd3e6;
  transition: background-color 0.3s ease-in;
}

button[type="submit"]:hover {
  background-color: #86b3da;
}

button[type="submit"]:active {
  background-color: #4298e4;
}

form {
  display: flex;
  gap: 2px;
}

#units {
  display: flex;
  gap: 0;
}

input[type="radio"] {
  display: none;
}

input[type="radio"]:checked + label {
  border-color: #bdd3e6;
  background-color: #bdd3e6;
  color: black;
}

.us-unit-label,
.metric-unit-label {
  padding: 1px 6px;
  cursor: pointer;
  transition:
    border-color 0.3s ease-in-out,
    background-color 0.3s ease-in-out;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.us-unit-label {
  border-color: #bdd3e6;
  color: #bdd3e6;
  border-style: solid;
  border-width: 2px 2px 2px 0;
  border-radius: 0 16px 16px 0;
}

.metric-unit-label {
  border-color: #bdd3e6;
  color: #bdd3e6;
  border-style: solid;
  border-width: 2px 0 2px 2px;
  border-radius: 16px 0 0 16px;
}

#loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #00000040;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  transition: opacity 0.3s ease;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 6px solid lightgray;
  border-top-color: dodgerblue;
  border-radius: 50%;
  animation: spin 1.2s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

#loading.hidden {
  opacity: 0;
  pointer-events: none;
}
